export default {
    data(){
        return{
            utilityUri:'/utility'
        }
    },
    methods:{
        onClickCardMenu(type){
            switch(type){
                case 'timer':
                    this.$router.push(this.utilityUri+'/timer')
            }
        }
    }
}
